<script lang="ts">
import { defineComponent } from 'vue'

type Product = {
    title: string
    description: string
    price: number
    currency: string
    id: string
    image: string
}

export default defineComponent({
    data() {
        return {
            count: 2,
            products: Array<Product>(),
        }
    },
    mounted() {
        fetch('https://mock.shop/api?query={products(first:%2020){edges%20{node%20{id%20title%20description%20featuredImage%20{id%20url}%20variants(first:%203){edges%20{node%20{price%20{amount%20currencyCode}}}}}}}}')
            .then(response => response.json())
            .then(source => {

                this.products = Array<Product>();

                for (let data of source.data.products.edges) {

                    this.products.push({
                        id: data.node.id,
                        title: data.node.title,
                        description: data.node.description,
                        price: data.node.variants.edges[0].node.price.amount,
                        currency: data.node.variants.edges[0].node.price.currencyCode,
                        image: data.node.featuredImage.url
                    });

                }
            })
    }
})
</script>



<template>
    <div class="bg-white">
        <div class="mx-auto max-w-2xl px-4 py-16 sm:px-6 sm:py-24 lg:max-w-7xl lg:px-8">
            <h2 class="text-2xl font-bold tracking-tight text-gray-900">Customers also purchased</h2>

            <div class="mt-6 grid grid-cols-1 gap-x-6 gap-y-10 sm:grid-cols-2 lg:grid-cols-4 xl:gap-x-8">
                <div v-for="product in products" :key="product.id" class="group relative">
                    <div
                        class="aspect-h-1 aspect-w-1 w-full overflow-hidden rounded-md bg-gray-200 lg:aspect-none group-hover:opacity-75 lg:h-80">
                        <img :src="product.image" :alt="product.title"
                            class="h-full w-full object-cover object-center lg:h-full lg:w-full" />
                    </div>
                    <div class="mt-4 flex justify-between">
                        <div>
                            <h3 class="text-sm text-gray-700">
                             
                                    <span aria-hidden="true" class="absolute inset-0" />
                                    {{ product.title }}
                                
                            </h3>
                            <p class="mt-1 text-sm text-gray-500">{{ product.id }}</p>
                        </div>
                        <p class="text-sm font-medium text-gray-900">{{ product.price }}</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>